export default {
	data() {
		return {
			current: 0,
			imgUrl: this.$imgUrl,
			info: '',
			dataFormList: []
		};
	},
	onLoad:function(){
		this._getMembersNewsBannerList()
		this._getMembersNewsList()
	},
	methods: {
		change (e) {
			this.current = e.detail.current;
		},
		// 获取banner图
		_getMembersNewsBannerList () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$member.post('Store/get_members_news_banner_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.info = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		// 获取列表
		_getMembersNewsList () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$member.post('Store/get_members_news_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.dataFormList = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		toMemberLatestNewsContent(item) {
			var items = JSON.stringify(item)
			uni.navigateTo({
				url: '../../pagesMember/MemberLatestNewsContent/MemberLatestNewsContent?items=' + items 
			})
		}
	}
}
